
import { defineComponent } from "vue";
export default defineComponent({
  ControlType: "Switch", // 
  nameCn: "",
  icon: "icon-kaiguanguan",
  formConfig: {
    data() {
      return {
        fieldName: "",
        label: "",
        tip: "",
        value: '',
        showRule: "{}",
        required: false,
        rule: "[]",
        default:  false,
      };
    },
    morenConfig() {
      return [
        {
          ControlType: "Switch",
          data: {
            fieldName: "default",
            tip: "",
            label: "",
            showRule: "{}",
            required: false,
            default: false,
            rule: "[]",
          },
        }
      ];
    },
  },
  props: {
    drag: Boolean,
    data: Object,
    item: Object,
  },
  watch: {
    data: {
      handler(){
        this.$emit('change')
      },
      deep: true
    }
  },
});
