
import { defineComponent, ref, toRefs, nextTick, watch, computed, reactive } from "vue";
import {copy, paste, onDelete, cut, onTop, onBottom} from '@/utils/shortcutKey'
import {useStore} from 'vuex'
export default defineComponent({
  props: {
    active: Boolean,
    currentIndex: Number,
    len: Number
  },
  mounted(){
    console.log(this.currentIndex)
    console.log(this.len)
  },
  setup(props, context) {
    let store = useStore();
    let isShow = ref(false);
    let editForm = ref();
    let {emit} = context
    let copyContent = computed(() => store.state.copyContent)
    let handleShortCut = (e: any) => {
      if (props.active) {
        e.preventDefault();
        isShow.value = true;
        let x = e.offsetX;
        let y = e.offsetY;
        nextTick(() => {
          editForm.value.style.left = x + "px";
          editForm.value.style.top = y + "px";
        });
      }
    };
    watch(
      () => props.active,
      () => {
        if (!props.active) {
          isShow.value = props.active;
        }
      }
    );
    let handleMenu = () => {
      isShow.value = false;
    };
    let handleActive = (type: string) => {
      if(type === 'copy'){
        copy()
      }else if(type === 'paste'){
        emit('paste')
        paste()
      }else if(type === 'cut'){
        cut()
      }else if(type === 'delete'){
        onDelete()
      }else if(type === 'top'){
        onTop()
      }else if(type === 'bottom'){
        onBottom()
      }
    }
    return {
      isShow,
      handleShortCut,
      handleMenu,
      editForm,
      handleActive,
      copyContent
    };
  },
});
