
import { defineComponent } from "vue";
export default defineComponent({
  ControlType: "ColorSelect", // 
  nameCn: "",
  icon: "icon-sen103",
  formConfig: {
    data() {
      return {
        fieldName: "",
        label: "",
        tip: "",
        placeholder: "",
        showRule: "{}",
        required: false,
        rule: "[]",
        default: '#409EFF',
      };
    },
    morenConfig() {
      return [
        {
          ControlType: "ColorSelect",
          data: {
            fieldName: "default",
            tip: "",
            label: "",
            placeholder: "",
            showRule: "{}",
            required: false,
            rule: "[]",
          },
        },
      ];
    },
  },
  props: {
    drag: Boolean,
    data: Object,
    item: Object,
  },
  watch: {
    data: {
      handler() {
        this.$emit("change");
      },
      deep: true,
    },
  },
});
