
import { defineComponent } from "vue";
export default defineComponent({
  ControlType: "Radio", // 
  nameCn: "",
  icon: "icon-danxuankuang",
  formConfig: {
    data() {
      return {
        fieldName: "",
        label: "",
        tip: "",
        value: "",
        showRule: "{}",
        required: false,
        rule: "[]",
        default: false,
        itemConfig: {
          value: "1",
          id: 1,
          items: [
            {
              label: "1",
              value: "1",
              select: true,
              id: 1,
            },
            {
              label: "2",
              value: "2",
              select: false,
              id: 2,
            },
          ],
        },
      };
    },
    morenConfig() {
      return [
        {
          ControlType: "KeyValueConfig",
          data: {
            fieldName: "itemConfig",
            tip: "",
            label: "",
            showRule: "{}",
            required: false,
            default: false,
            rule: "[]",
          },
        },
      ];
    },
  },
  props: {
    drag: Boolean,
    data: Object,
    item: Object,
  },
  watch: {
    data: {
      handler(){
        this.$emit('change')
      },
      deep: true
    }
  },

});
