
import { defineComponent, reactive, ref, onMounted } from "vue";
import _ from "@/utils/_";
export default defineComponent({
  props: {
    allFormList: Array,
    formResult: Object,
  },
  setup(props) {
    let { allFormList, formResult } = reactive(props);
    let rules: any = ref({});
    let ruleForm = ref();
    let controlObj = ref();
    allFormList?.forEach((item: any) => {
      let rule: any[] = [];
      if (item.data.required) {
        rule.push({
          required: true,
          message: "" + item.data.label,
          trigger: "blur",
        });
      }
      rule = rule.concat(_.tryParseJson(item.data.rule));
      // jsoneditor
      if (item.data.json) {
        rule.push(..._.getJsonValidate());
      }
      rules.value[item.data.fieldName] = rule;
    });
    let handleControlChange = () => {
      let allFormLists: any = allFormList;
      let formResults: any = formResult;
      allFormLists.forEach((item: any) => {
        if (item.data.showRule === "{}") {
          item.show = true;
        } else {
          let showRule;
          try {
            showRule = JSON.parse(item.data.showRule);
            for (let key in showRule) {
              if (_.getDataType(formResults[key]) == "Array") {
                let isHave = false; // 
                // 
                if (_.getDataType(showRule[key]) == "Array") {
                  showRule[key].forEach((rule: any) => {
                    if (formResults[key].indexOf(String(rule)) > -1) {
                      isHave = true;
                    }
                  });
                } else {
                  if (formResults[key].indexOf(String(showRule[key])) > -1) {
                    isHave = true;
                  }
                }
                item.show = isHave;
              } else {
                if (formResults[key] == showRule[key]) {
                  item.show = true;
                } else {
                  item.show = false;
                }
              }
            }
          } catch (e) {
            item.show = true;
          }
        }
      });
    };
    onMounted(handleControlChange);
    return {
      rules,
      ruleForm,
      controlObj,
      handleControlChange,
    };
  },
});
