
import { defineComponent } from "vue";
import _ from "@/utils/_";
export default defineComponent({
  ControlType: "JsonEditor", // 
  nameCn: "JSON",
  icon: "icon-json-full",
  rule: _.getJsonValidate(),
  formConfig: {
    data() {
      return {
        fieldName: "",
        label: "",
        tip: "",
        showRule: "{}",
        required: false,
        rule: "[]",
        default: "[]",
        json: true,
      };
    },
    morenConfig() {
      return [
        {
          ControlType: "JsonEditor",
          data: {
            fieldName: "default",
            tip: "",
            label: "",
            placeholder: "",
            showRule: "{}",
            required: false,
            rule: "[]",
          },
        },
      ];
    },
  },
  props: {
    drag: Boolean,
    data: Object,
    item: Object,
  },
  data() {
    interface jsonEditor {
      [key: string]: any;
    }
    let jsonEditorType: jsonEditor = {};
    return {
      jsonEditor: jsonEditorType,
      jsonEditorDialog: jsonEditorType,
    };
  },
  watch: {
    item: {
      handler(newValue) {
        if (this.drag) {
          this.jsonEditor.set(_.tryParseJson(newValue.data.default));
        } else {
          let data: any = this.data;
          let item: any = this.item;
          this.jsonEditor.set(_.tryParseJson(data[item.data.fieldName]));
        }
      },
      deep: true,
    },
    data: {
      handler() {
        this.$emit("change");
      },
      deep: true,
    },
  },
  methods: {
    showCustomDialog() {
      let myDialog: any = this.$refs.myDialog;
      myDialog.show();
      myDialog.init("JSON", "icon-json-full");
      this.$nextTick(() => {
        const container = this.$refs.JsonViewerDialog;
        const options = {
          modes: ["text", "code", "view"],
          mode: "code",
          search: false,
        };
        this.jsonEditorDialog = new window.JSONEditor(container, options);
        this.jsonEditorDialog.set(_.tryParseJson(this.jsonEditor.getText()));
      });
    },
    closeDialog() {
      let myDialog: any = this.$refs.myDialog;
      myDialog.close();
    },
    saveJson() {
      this.jsonEditor.set(_.tryParseJson(this.jsonEditorDialog.getText()));
      let data: any = this.data;
      let item: any = this.item;
      let fieldName = item.data.fieldName;
      data[fieldName] = this.jsonEditor.getText();
      let myDialog: any = this.$refs.myDialog;
      myDialog.close();
    },
    initJson() {
      const container = this.$refs.jsoneditor;
      let data: any = this.data;
      let item: any = this.item;
      let fieldName = item.data.fieldName;
      let that = this;
      const options = {
        modes: ["text", "code", "view"],
        mode: "code",
        search: false,
        onChange() {
          data[fieldName] = that.jsonEditor.getText();
        },
      };
      this.jsonEditor = new window.JSONEditor(container, options);
      if (this.drag) {
        this.jsonEditor.set(_.tryParseJson(item.data.default));
      } else {
        this.jsonEditor.set(_.tryParseJson(data[item.data.fieldName]));
      }
    },
  },
  mounted() {
    this.initJson();
  },
});
