
import { defineComponent } from "vue";
export default defineComponent({
  ControlType: "DateTime", // 
  nameCn: "",
  icon: "icon-riqishijian",
  formConfig: {
    data() {
      return {
        fieldName: "",
        label: "",
        tip: "",
        placeholder: "1",
        showRule: "{}",
        required: false,
        rule: "[]",
        default: "1",
      };
    },
    morenConfig() {
      return [
        {
          ControlType: "DateTime",
          data: {
            fieldName: "default",
            tip: "",
            label: "",
            placeholder: "1",
            showRule: "{}",
            required: false,
            rule: "[]",
          },
        },
        {
          ControlType: "Text",
          data: {
            fieldName: "placeholder",
            tip: "",
            label: "",
            placeholder: "1",
            showRule: "{}",
            required: false,
            rule: "[]",
          },
        },
      ];
    },
  },
  props: {
    drag: Boolean,
    data: Object,
    item: Object,
  },
  watch: {
    data: {
      handler() {
        this.$emit("change");
      },
      deep: true,
    },
  },
});
