
import { defineComponent } from "vue";
export default defineComponent({
  ControlType: "Slider", // 
  nameCn: "",
  icon: "icon-icon_huakuai",
  formConfig: {
    data() {
      return {
        fieldName: "",
        label: "",
        tip: "",
        showRule: "{}",
        required: false,
        rule: "[]",
        default: 0,
        min: 0,
        max: 100
      };
    },
    morenConfig() {
      return [
        {
          ControlType: "InputNumber",
          data: {
            fieldName: "default",
            tip: "",
            label: "",
            placeholder: "",
            showRule: "{}",
            required: false,
            rule: "[]",
          },
        },
        {
          ControlType: "Text",
          data: {
            fieldName: "min",
            tip: "",
            label: "",
            placeholder: "",
            showRule: "{}",
            required: false,
            rule: "[]",
          },
        },
        {
          ControlType: "Text",
          data: {
            fieldName: "max",
            tip: "",
            label: "",
            placeholder: "",
            showRule: "{}",
            required: false,
            rule: "[]",
          },
        },
      ];
    },
  },
  props: {
    drag: Boolean,
    data: Object,
    item: Object,
  },
  watch: {
    data: {
      handler(){
        this.$emit('change')
      },
      deep: true
    }
  },
});
