
import { defineComponent } from "vue";

export default defineComponent({
  props: {
    dialogclass: String,
    showDialog: Boolean
  },
  data() {
    return {
      isshow: false,
      title: "",
      icon: "",
      isFullScreen: false,
    };
  },
  methods: {
    init(title: string, icon: string) {
      this.title = title;
      this.icon = icon;
    },
    show() {
      this.isshow = true;
      this.$emit("open");
    },
    close() {
      this.isshow = false;
      this.$emit("close");
    },
  },
});
